import { createRouter, createWebHistory } from "vue-router";
import Home from '../components/HomePage.vue'
import AddPizza from '../components/AddPizza.vue'
import OrderPizza from '../components/OrderPizza.vue'
import PizzaProducts from '../components/PizzaProducts.vue'
import AboutUs from '../components/AboutUs.vue'
import ContactUs from '../components/ContactUs.vue'



const routes = [
    {
        path:'/',
        name:'home',
        component:Home
    },
    {
        path:'/add',
        name:'addPizza',
        component:AddPizza
    },
    {
        path:'/order',
        name:'OrderPizza',
        component:OrderPizza
    },
    {
        path:'/view',
        name:'PizzaProducts',
        component:PizzaProducts
    },
    {
        path:'/about',
        name:'AboutUs',
        component:AboutUs
    },
    {
        path:'/contacts',
        name:'ContactUs',
        component:ContactUs
    }

]

const router = createRouter({
    history:createWebHistory(process.env.BASE_URL),
    routes
})

export default router;